@import "../../assets/foundation/color";

#LandingSection2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-basis: 100%;
  margin: -20px 0 40px 0;

  .top {
    text-align: center;

    h2 {
      color: $primary-text;
    }

    sub {
      color: $tertiary-text;
      font-weight: 300;
      font-stretch: normal;
      font-style: normal;
      letter-spacing: normal;
      font-size: 1.1em;
      line-height: 1.82;
      text-align: right;
    }

    .selector {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 25px;

      .selector-item {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100px;
        height: 100px;
        padding: 15px 5px;
        padding-bottom: 0;
        margin: 5px 10px;
        border: 1px solid #ececec;
        border-radius: 10px;
        z-index: 2;

        img {
          cursor: pointer;
          width: 40px;
          height: auto;
        }

        span {
          color: $primary-text;
        }

        &.active {
          border-color: $primary-nav-text;
          background-color: $secondary-fab;
        }
      }
    }
  }

  .bottom {
    display: flex;
    align-items: center;
    width: 100%;
    min-height: inherit;
    margin-top: -125px;

    .right {
      width: 25%;
      background-image: url("../../assets/images/cloud-star-group2.svg");
      background-repeat: no-repeat;
      background-size: auto 100%;
      background-position: right center;
      min-height: 528px;
      padding-left: 35px;
      .image {
        position: relative;
        width: 238px;
        min-height: inherit;
        margin-right: auto;
        img {
          position: absolute;
          top: calc(50% + 75px);
          transform: translateY(-50%);
        }
      }
    }

    .left {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      width: 75%;
      .desc{
        position: relative;
        top: 85px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        direction: rtl;
        text-align: right;
        padding-left: 30%;
        padding-right: 15px;
        .title{
          color: $quaternary-text;
          font-weight: normal;
        }
        .text{
          color: $primary-nav-text;
          line-height: 2.67;
          a{
            color: $primary-link-text;
            margin-right: 5px;
            border-bottom: 2px solid #ededed;
            border-radius: 1px;
            padding-bottom: 5px;
          }
        }
      }
    }
  }
  @media only screen and (max-width: 960px) {
    box-sizing: border-box;
    margin-top: 25px;
    .top{
      box-sizing: border-box;
      text-align: center;
      padding: 0 10px;
      sub{
        padding: 5px 15px;
      }
      .selector{
        box-sizing: border-box;
        .selector-item{
          width: 85px;
          height: 85px;
          padding: 0;
          margin: 5px 2.5px;
          span{
            font-size: 0.75em;
          }
          img{
            width: 30px;
            height: 30px;
          }
        }
      }
    }
    .bottom{
      box-sizing: border-box;
      position: relative;
      margin: -25px 0 25px 0;
      .right{
        box-sizing: border-box;
        width: 100%;
        background-position: 100px center;
      }
      .left{
        box-sizing: border-box;
        position: absolute;
        width: 100%;
        padding: 0 5px;
        .desc{
          padding: 0 5px;
          top: 25px;
          .title{
            padding: 15px 5px;
            padding-right: 20px;
          }
          .text{
            padding: 5px 20px;
          }
          .image{
            display: flex;
            align-items: center;
            width: 95%;
            max-height: 125px;
            margin: 0 auto;
            overflow: hidden;
            border-radius: 10px;
            img{
              width: 100%;
              height: 100%;
            }
          }
        }
      }
    }
  }
}