@import "../../assets/foundation/color";

.cleanup-btn {
  border: none;
  border-radius: 23px;
  background-color: $primary-btn;
  color: $primary-btn-text;
  box-shadow: none;
  transition: all 300ms ease-in-out;

  &.cleanup-btn-primary {
    background-color: $primary-btn;
    color: $primary-btn-text;
    &.cleanup-btn-shadow-self {
      box-shadow: rgba($color: $primary-btn, $alpha: 0.28) 0 0 25px;
    }
  }

  &.cleanup-btn-secondary {
    background-color: $secondary-btn;
    color: $secondary-btn-text;
    &.cleanup-btn-shadow-self {
      box-shadow: rgba($color: $secondary-btn, $alpha: 0.28) 0 0 25px;
    }
  }

  &.cleanup-btn-tertiary {
    background-color: $tertiary-btn;
    color: $tertiary-btn-text;
    &.cleanup-btn-shadow-self {
      box-shadow: rgba($color: $tertiary-btn, $alpha: 0.28) 0 0 25px;
    }
  }

  &.cleanup-btn-shadow-lighter {
    box-shadow: $lighter-shadow 0 0 10px;
  }

  &.cleanup-btn-shadow-light {
    box-shadow: $light-shadow 0 0 10px;
  }

  &.cleanup-btn-shadow-mod {
    box-shadow: $mod-shadow 0 0 10px;
  }

  &.cleanup-btn-shadow-none {
    box-shadow: none;
  }

  &.cleanup-btn-border {
    border: solid 1px;
  }

  &.cleanup-btn-small {
    font-size: small;
    padding: 5px 15px;
  }

  &.cleanup-btn-medium {
    font-size: medium;
    padding: 7px 25px;
  }

  &.cleanup-btn-large {
    font-size: large;
    padding: 10px 28px;
  }

  &:hover{
    box-shadow: inset #222 0 0 2px;
  }
}