@import "../../assets/foundation/color";

#LandingSection3 {
  display: block;
  margin: 70px 0;
  padding: 25px 0;
  background-color: $secondary;
  .top{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5px;
    h2{
      color: $primary-nav-text;
    }
    sub{
      color: $tertiary-text;
    }
  }
  .bottom{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 25px;
    margin-bottom: 5px;
    .item{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      direction: rtl;
      padding: 5px 20px;
      .fabric{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 130px;
        height: 130px;
        background-color: $primary;
        border-radius: 100%;
        box-shadow: 0 0 10px rgba(68, 88, 151, 0.1);
        .image{
          width: 85px;
          height: 85px;
          background-color: $secondary-fab;
          border-radius: 100%;
          img{
            width: 50px;
          }
        }
      }
      .text{
        h5{
          color: $primary-btn-text;
          margin-top: 30px;
          margin-bottom: 0;
          font-weight: 700;
          font-size: 1.1em;
        }
        sub{
          color: $primary-btn-text;
          font-size: 0.85em;
        }
      }
    }
  }
  @media only screen and (max-width: 960px) {
    box-sizing: border-box;
    .top{
      box-sizing: border-box;
      padding: 0 15px;
      text-align: center;
    }
    .bottom{
      box-sizing: border-box;
      flex-direction: column;
      .item{
        margin-bottom: 25px;
      }
    }
  }
}