@import "../../assets/foundation/color"; 

#LandingSection1 {
  display: flex;
  align-items: center;
  flex-basis: 100%;

  .left {
    width: 45%;
    background-image: url("../../assets/images/cloud-star-group.svg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: -75px 50%;
    min-height: 486px;
    .slider-parent{
      position: relative;
      width: 69%;
      min-height: inherit;
      margin-left: auto;
      .slider{
        position: absolute;
        top: calc(50% - 28px);
        right: 10px;
        transform: translateY(-50%);
        object-fit: contain;
        border-radius: 5px;
      }
    }
  }

  .right {
    width: 55%;
    padding-right: 100px;
    h1{
      color: $primary-text;
    }
    p{
      color: $secondary-text;
      font-weight: 300;
      font-stretch: normal;
      font-style: normal;
      letter-spacing: normal;
      line-height: 2.67;
      text-align: right;
    }
    .group-btns{
      margin-top: 25px;
      margin-left: auto;
      text-align: right;
      button{
        margin: 0 5px;
      }
    }
  }
  @media only screen and (max-width: 960px) {
    box-sizing: border-box;
    flex-direction: column-reverse;
    min-height: 310px;
    margin-top: 25px;
    margin-bottom: 50px;
    .left{
      box-sizing: border-box;
      width: 100%;
      min-height: inherit;
      margin-top: -50px;
      background-position: -100px top;
      background-size: cover;
      .slider-parent{
        box-sizing: border-box;
        width: 90%;
        margin: 0 auto;
        margin-top: 45px;
      }
    }
    .right{
      position: relative;
      box-sizing: border-box;
      width: 100%;
      padding: 0 15px;
      .group-btns{
        position: absolute;
        bottom: -125%;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 5;
        button{
          padding: 10px 20px;
        }
      }
    }
  }
}