@import "../../assets/foundation/color";

.cleanup-navlink {
  border: none;
  border-bottom: 1px dotted transparent;
  border-radius: 10px/25px 10px;
  background-color: transparent;
  color: $primary-nav-text;
  box-shadow: none;
  transition: all 500ms ease-in-out;

  &.cleanup-navlink-primary {
    color: $primary-nav-text;
  }

  &.cleanup-navlink-secondary {
    color: $secondary-nav-text;
  }

  &.cleanup-navlink-tertiary {
    color: $tertiary-nav-text;
  }

  &:hover{
    border-bottom-color: #ccc;
  }
}