@import "../../assets/foundation/color";

.cleanup-slider {
  .displayer {
    position: relative;

    .svg {
      cursor: pointer;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 1;

      &:hover{
        & ~ img:last-child{
          filter: contrast(75%);
        }
      }
      
      &.triangle {
        left: 50.5%;
        width: 32px;
        height: auto;
        transform: translate(-50%, -50%) rotate(-90deg);
      }
    }

    .circle {
      fill: $tertiary-fab;
    }
  }

  .thums{
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin-top: 5px;
    .thum{
      width: 10px;
      height: 10px;
      background-color: rgba($color: $quaternary-text, $alpha: 0.28);
      border-radius: 100%;
      margin: 0 2px;
      &.active{
        background-color: $quaternary-text;
      }
    }
  }
}