@import '../../assets/foundation/color';

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  min-height: 206px;
  background-color: $secondary-fab;

  .left {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    width: 25%;
    height: 100%;
    min-height: 206px;
    padding: 0 25px;
    color: $secondary-nav-text;
    background-color: rgba($color: $primary, $alpha: 0.7);

    span {
      font-size: 0.9em;
      margin: 5px;
      text-align: center;
    }

    .tel {
      font-family: iran-sans-fa-num;
      font-size: 1.5em;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.36;
      letter-spacing: 5.5px;
      text-align: left;
    }

    .social {
      span {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        background-color: $primary;
        width: 40px;
        height: 40px;
        border-radius: 100%;
        box-shadow: 0 0 6px rgba(0, 0, 0, 0.1);

        svg {
          color: $tertiary-nav-text;
          font-size: 1.85em;
        }
      }
    }
  }

  .right {
    width: 75%;
    display: flex;
    justify-content: center;
    align-items: center;

    .signs {
      display: flex;
      align-items: center;
      width: 25%;

      .sign {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 164px;
        margin-left: 25px;
        padding: 0 8px;
        background-color: $primary;
        border-radius: 19px;

        img {
          cursor: pointer;
          width: 115px;
        }
      }
    }

    .navs {
      display: flex;
      justify-content: center;
      align-items: flex-start;
      width: 75%;

      .nav {
        padding: 5px 15px;
        margin-right: 5%;
        text-align: right;
        direction: rtl;

        h3 {
          color: $tertiary-nav-text;
          font-weight: 900;
        }

        ul {
          list-style: none;
          padding: 0;

          li {
            color: $secondary-nav-text;

            button {
              text-align: right;
              padding-right: 0;
            }
          }
        }
      }
    }

    @media only screen and (min-width: 960px) and (max-width: 1200px) {
      .signs {
        flex-direction: column;
        padding: 15px 10px;

        .sign {
          margin: 10px 0;
        }
      }

      .navs {
        .nav {
          padding: 5px;
        }
      }
    }
  }

  .bottom {
    display: block;
    width: 100%;
    margin: 0 auto;
    text-align: center;

    .logo {
      display: flex;
      flex-direction: row-reverse;
      justify-content: center;
      align-items: center;
      padding: 20px 5px;
      background-color: $primary;
      color: $tertiary-text;

      img {
        width: 30px;
        height: auto;
        filter: grayscale(85%);
      }
    }
  }

  @media only screen and (max-width: 960px) {
    flex-direction: column;

    .left {
      align-items: center;
      justify-content: center;
      width: 100%;
    }

    .right {
      box-sizing: border-box;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;

      .signs {
        box-sizing: border-box;
        justify-content: center;
        align-items: center;
        width: 100%;
        padding: 15px 10px;

        .sign {
          box-sizing: border-box;
          margin: 0 5px;
        }
      }

      .navs {
        box-sizing: border-box;
        width: 100%;

        .nav {
          padding: 5px 10px;
          margin: 0 5px;
          text-align: center;

          ul {
            li {
              button{
                text-align: center;
              }
            }
          }
        }
      }
    }
  }
}