@import "../../assets/foundation/color";

#LandingSection4 {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 300px;
  margin-top: -15px;
  margin-bottom: 30px;

  .left {
    width: 25%;
    min-height: inherit;
    background-image: url("../../assets/images/plant.svg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 0 50%;
  }

  .center {
    width: 50%;
    text-align: center;

    .top {
      margin-bottom: 50px;

      h2 {
        color: $primary-text;
        margin-bottom: 10px;
        font-weight: 500;
      }

      sub {
        color: $tertiary-text;
      }
    }

    .bottom {
      margin-top: 15px;
    }
  }

  .right {
    width: 25%;
    min-height: inherit;
    background-image: url("../../assets/images/cleaner.svg");
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: 101% 50%;
  }

  @media only screen and (max-width: 960px) {
    position: relative;
    min-height: 400px;
    margin-bottom: 0;

    .left {
      display: none;
    }

    .center {
      position: absolute;
      width: 100%;
      min-height: inherit;
      z-index: 10;

      .top {
        padding: 0 15px;
      }

      .bottom {
        text-align: left;
        margin-left: 11%;
      }
    }

    .right {
      position: absolute;
      bottom: 50px;
      width: 100%;
      min-height: 200px;
      background-position: 175px center;
    }
  }
}