@import "../../assets/foundation/color";

#Header {
  margin: 29px 10%;

  .header {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-basis: 100%;
    padding: 10px 15px;
    box-shadow: $lighter-shadow 0 8px 15px;
    border-radius: 40px;

    .nav-right {
      flex-grow: 2;
      display: flex;
      justify-content: flex-end;
      align-items: center;

      .nav-group {
        display: flex;
        flex-direction: row-reverse;

        .nav-item {
          margin-left: 5px;

          .register-btn {
            border-color: $tertiary-text;
          }
        }

        .nav-logo {
          display: flex;
          flex-direction: row;
          justify-content: flex-end;
          align-items: center;
          direction: rtl;
          margin-left: 13px;
          color: $logo-name;
          font-size: 16px;
          font-weight: 900;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.56;
          letter-spacing: normal;
          text-align: left;

          img {
            width: 30.4px;
          }
        }
      }
    }

    .nav-left {
      flex-grow: 1;
      display: flex;
      justify-content: flex-start;
      align-items: center;

      .nav-item {
        .locked-padlock {
          width: 10px;
          height: 13px;
          object-fit: contain;
        }
      }
    }
  }

  @media only screen and (max-width: 960px) {
    margin: 0;

    .header {
      box-sizing: border-box;
      padding: 0 15px;
      border-radius: 0;

      .nav-left {
        box-sizing: border-box;

        .nav-item {
          .register-btn {
            border: none;
            background-color: $secondary-btn;
          }
        }
      }

      .nav-right {
        box-sizing: border-box;
        .nav-group{
          box-sizing: border-box;
          .nav-item{
            box-sizing: border-box;
            display: flex;
            justify-content: center;
            align-items: center;
            &.nav-logo{
              margin: 0;
            }
          }
        }
      }
    }
  }
}